@import "./Utilities/Variables";
@import "./Utilities//Mixins";
@import "./Utilities//BreakPoints";

.parentContainer{
    width: 100%;
    background-color: $color-secondary;
    padding: 20px 0px;
}

.mainContainer{
    @include d-flex(center, start, row, 0px);
}

.footer{
    width: 90%;
    padding: 20px 0px;
    @include d-flex(start, start, row, 20px);
    color: white;

    &__leftSide{
        width: 50%;
       

        &__aboutUs{
            padding: 20px 0px;
        }
    }

    &__rightSide{
        width: 50%;
        

        &__info{
            padding: 45px 0px 18px 0px;
        }

        &__socialNetworks{
            font-size: 35px;
            @include d-flex(start, start, row, 30px);
        }
    }
}
.creators{
    text-align: center;
    color: white;
}

@include combinedMobileAndTabletScreens {
  

    .footer{
        @include d-flex(start, start, column, 20px);

        &__leftSide{
            width: 100%;
        }

        &__rightSide{
            width: 100%;
        }
    }
}